<!-- eslint-disable vue/multi-word-component-names -->
<template>
  <div>
    <Headercomponer title="搜索查询">
      <!-- 标题及操作区 -->
      <div slot="extra">

      </div>

      <!-- 主要内容 -->
      <div>
        <!-- 搜索排序 -->
        <el-form :model="formModel" :inline="true" >
          <el-form-item label="需要条数">
            <el-select v-model="formModel.everyNum" placeholder="需要条数">
              <el-option label="30" value="30"></el-option>
              <el-option label="50" value="50"></el-option>
              <el-option label="100" value="100"></el-option>
              <el-option label="200" value="200"></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="搜索关键字">
            <el-autocomplete
                class="inline-input"
                v-model="formModel.searchText"
                :fetch-suggestions="querySearch"
                placeholder="请输入搜索内容"
                @select="handleSelect"
                clearable
              >
            </el-autocomplete>
          </el-form-item>

          <el-form-item label="排序">
            <el-select v-model="formModel.how" placeholder="排序方式">
              <el-option label="价格" value="price"></el-option>
              <el-option label="评论数" value="eval"></el-option>
            </el-select>
          </el-form-item>

          <el-form-item>
            <el-select v-model="formModel.asc" placeholder="升降序">
              <el-option label="升序" value="asc"></el-option>
              <el-option label="降序" value="desc"></el-option>
            </el-select>
          </el-form-item>

          <el-form-item>
            <el-button @click="getSearchList" type="success">筛选</el-button>
          </el-form-item>
        </el-form>

        <!-- 列表 -->
        <el-table :data="pageData" max-height="880px" v-loading="loading">
          <el-table-column
            label="商品ID"
            prop="goodsId"
            width="320">
          </el-table-column>

          <el-table-column
            label="商品图片"
            prop=""
            width="400">
            <template #default="{row}">
              <div class="goods-img">
                <img ref="imgRef" :src="row.imageUrl" @error="onError(row)">
              </div>
            </template>
          </el-table-column>

          <el-table-column
            label="商品名称"
            prop="goodsName">
          </el-table-column>

          <el-table-column
            label="商品价格"
            prop=""
            width="220">
            <template #default="{row}">￥{{ row.goodsPrice.toFixed(2) }}</template>
          </el-table-column>

          <el-table-column
            label="商品数量"
            prop="evalNum"
            width="180">
          </el-table-column>

        </el-table>

        <!-- 分页 -->
        <el-pagination
          background
          @current-change="handleCurrentChange"
          :current-page.sync="formModel.page"
          :page-size="30"
          layout="prev, pager, next, total"
          :total="searchList.length">
        </el-pagination>
      </div>
    </Headercomponer>
  </div>
</template>

<script>
import defaultPic from '@/assets/A6XaeFUerS.jpg'
import { getSearchSuggeList, getSearchGoodsList } from '@/api/search'
import Headercomponer from '@/components/headercomponer.vue'
export default {
  data () {
    return {
      loading: false,
      formModel: {
        searchText: '', // 搜索关键字
        page: '1', // 需要第几页数据
        everyNum: '30', // 当前页需要的数据条数
        how: 'price', // 排序关键词
        asc: 'asc' // 升序或降序
      },
      suggeList: [],
      searchList: [],
      page: 1,
      defaultPic // 加载失败图片
    }
  },
  methods: {
    // 获取搜索列表
    async getSearchList () {
      this.loading = true
      const { data: { list } } = await getSearchGoodsList(this.formModel)
      this.searchList = list.data
      this.loading = false
    },

    querySearch (queryString, cb) {
      cb(this.suggeList)
    },
    handleSelect (item) {},

    onError (row) {
      this.searchList.forEach(i => {
        if (i.goodsId === row.goodsId) {
          i.imageUrl = this.defaultPic
        }
      })
    },

    handleCurrentChange (page) {
      this.page = page
      this.getSearchList()
    }
  },
  async created () {
    this.$store.commit('tag/selecMenu', {
      path: this.$route.path,
      label: '搜索查询'
    })

    // 获取搜索列表
    this.getSearchList()

    // 获取搜索建议
    const { data: { list } } = await getSearchSuggeList()
    list.forEach(i => {
      this.suggeList.unshift({ value: i.searchText })
    })
  },
  computed: {
    pageData () {
      const start = (this.page - 1) * 30
      const end = start + 30
      return this.searchList.slice(start, end)
    }
  },
  components: {
    Headercomponer
  }
}
</script>

<style lang="less" scoped>
.goods-img{
  width: 100px;
  height: 100px;
  img{
    width: 100px;
  height: 100px;
  }
}
</style>
